<template>
	<view class="details-view">
		<view class="order-top">
			<view class="order-remind">
				<view class="order-title">支付成功，坐等开吃</view>
				<view>菜品已在制作中</view>
			</view>
		</view>
		<view class="food-list">
			<view class="food-number">
				<view class="food-num-card">
					<text class="card-table-num">桌号</text>
					<text class="card-food-num">{{other_data.tableName}}</text>
				</view>
				<view class="food-num-card">
					<text class="card-table-num">取餐码</text>
					<text class="card-food-num">{{other_data.deliverCode}}</text>
				</view>
			</view>
			<!-- 就餐方式 -->
			<view class="food-dining">
				<text class="food-dining-title">就餐方式</text>
				<text class="food-dining-info" v-if="!other_data.packageAmount">店内堂食</text>
				<text class="food-dining-info" v-else>打包</text>
			</view>
			<view class="foot-back">
				<block v-for="(item,index) in  orderList" :key="index">
					<view class="foot-til">
						<text>第{{ orderList.length - index}}份</text>
						<text>下单成功，坐等开吃</text>
					</view>

					<block>
						<view class="foot-deta">
							<view>
								<image :src="item.image" mode="aspectFill"></image>
							</view>
							<view class="foot-name">
								<text>{{item.name}}</text>
								<text v-show="item.good_specs != ''">{{item.good_specs}}</text>
								<text>{{item.quantity}}{{item.unit}}</text>
							</view>
							<view class="foot-total">¥{{item.total_price}}</view>
						</view>
					</block>
					<!-- 展示更多 -->
					<view class="expand-more" v-show="item.max > 3" @click="open(index)">
						<text>展开全部</text>
						<image src="/static/tab/zhankai.svg"></image>
					</view>
				</block>
				<!-- 总计 -->
				<view class="total-view">
					<view>共{{orderList.length}}份</view>
					<view class="total-price">
						<text>总计</text>
						<text>¥{{other_data.amount}}</text>
					</view>
				</view>
			</view>
			<!-- 订单号 -->
			<view class="foot-back order-number">
				<text>订单编号：{{other_data.orderNumber}}</text>
				<text>下单时间：{{other_data.orderTime}}</text>
				<text>桌台名称：{{other_data.tableName}}</text>
			</view>
			<view style="height: 300rpx;"></view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: '', //订单id
				overall: 1, //总共多少份
				other_data: {},
				orderList: [],
				comp_data: [],
				goods_data: null,
				close_dish: true,
				orderListIndex: undefined,
			}
		},
		computed: {
			//商品信息
			setOrderList() {
				if (this.id) {
					this.orderList = this.$store.state.order_store.orderListInfo

					return 0
				}
				this.orderList = this.$store.state.shop_cart_store.shopPingCartDate

				return 0
			},
			//订单信息
			setOther_data() {
				if (this.id) {
					this.other_data = this.$store.state.order_store.checkOrderList.records[this.orderListIndex]
					return 0
				}
				this.other_data = this.$store.state.order_store.orderInfo
				return 0
			}
		},
		methods: {


			open(index) {
				this.$set(this.goods_data[index], 'goods_list', this.comp_data[index].goods_list)
				this.$set(this.goods_data[index], 'max', 0)
			},
			//支付
			pay() {
				uni.showModal({
					title: '提示',
					content: '确认支付',
					success: (res) => {
						if (res.confirm) {
							uni.reLaunch({
								url: './my-pay'
							})

						}
					}
				})
			}
		},
		async onLoad(option) {
			let id = option.id
			if (id) {
				this.id = id
				this.orderListIndex = option.index
			}

		},
		//销毁之前
		beforeDestroy() {
			clearTimeout()

		}
	}
</script>

<style>
	page {
		background-color: #f4f4f4;
	}

	.details-view {
		position: relative;
	}

	.order-top {
		/* background: linear-gradient(to bottom, #f7d45f, #f7d562, #f8d561, #f9db76, #f9de80);
		*/
		background: limegreen;
		height: 300rpx;
		color: white;
		font-size: 18px;

	}

	.order-remind view:nth-child(1) {
		padding-bottom: 20rpx;
		font-size: 24px;
		font-weight: bolder;
	}

	.order-remind {
		height: 200rpx;
		padding: 50rpx 0 0 50rpx;
	}



	.food-list {
		position: absolute;
		top: 200rpx;
		left: 20rpx;
		right: 20rpx;
	}

	.food-dining {
		background-color: greenyellow;
		border-radius: 10px;
		margin-top: 20px;
		width: 95%;
		height: 30px;
		display: flex;
		justify-content: space-between;
		padding: 10px;
		background: white;
		line-height: 30px;
		margin-bottom: 20px;
	}

	.food-dining-title {
		font-weight: bolder;
	}

	.food-dining-info {
		margin-right: 10px;
	}


	.food-number {
		/* padding: 0 20rpx; */
		margin-bottom: 20px;
		background: white;
		width: 100%;
		border-radius: 10px;
		height: 100px;
		display: flex;
		justify-content: space-around;
		align-items: center;

	}

	.food-num-card {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.food-table-num {
		font-size: 14px;

	}

	.card-food-num {
		font-size: 26px;
		font-weight: bolder;
		color: blue;
		margin-top: 5px;
	}


	.foot-back {
		background-color: #fefefe;
		border-radius: 10rpx;
		padding: 0 20rpx;
		margin-bottom: 30rpx;
	}

	.foot-til {
		height: 100rpx;
		color: #999999;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.foot-deta image {
		display: block;
		width: 130rpx;
		height: 130rpx;
		border-radius: 10rpx;
	}

	.foot-deta {
		display: flex;
		justify-content: space-between;
		height: 130rpx;
		margin: 40rpx 0;
	}

	.foot-name {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 0 20rpx;
		font-size: 30rpx;
	}

	.foot-name text:nth-child(1) {
		font-size: 31rpx !important;
		font-weight: bold;
	}

	.foot-name text:nth-child(2) {
		color: #666666;
	}

	.foot-total {
		font-weight: bold;
	}

	/* 展开更多 */
	.expand-more image {
		width: 25rpx;
		height: 25rpx;
		display: block;
		padding-left: 10rpx;
	}

	.expand-more {
		display: flex;
		align-items: center;
		justify-content: center;
		color: #999999;
		font-size: 25rpx;
		padding: 30rpx 0;
		border-bottom: 1rpx solid #f1f1f2;
	}

	/* 总计 */
	.total-price {
		display: flex;
		align-items: center;
		color: #333333;
		padding-left: 40rpx;
	}

	.total-price text:nth-child(2) {
		font-size: 35rpx;
		font-weight: bold;
		padding-left: 30rpx;
	}

	.total-view {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 30rpx 0;
	}

	.total-view view:nth-child(1) {
		color: #999999;
	}

	/* 订单号 */
	.order-number text {
		display: block;
		padding: 15rpx 0;
		font-size: 28rpx;
		color: #999999;
	}

	/* 加菜 */
	.add-a-dish {
		background-color: #fefefe;
		height: 120rpx;
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		box-shadow: 0rpx -1.9rpx 1rpx 1rpx #f9f9f9;
		padding: 0 20rpx;
		z-index: 9;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

	.add-a-dish view {
		background: linear-gradient(to right, #f8da81, #f8d771, #f7d362, #f6cb4a);
		width: 200rpx;
		height: 75rpx;
		line-height: 75rpx;
		text-align: center;
		border-radius: 50rpx;
		font-weight: bold;
		margin-left: 10rpx;
	}
</style>